import React, { useState } from "react";
import { Box, Anchor, Tabs, Tab, Heading, Button, TextInput } from "grommet";
import { Facebook, Tools } from "grommet-icons";
import { assign } from '../helpers/index'
import Parse, { signUp, resetPassword, fblogIn } from "../library/parse";
const Component = ({ onClose }) => {
  const [model, setModel] = useState({});
  const [message, setMessage] = useState("");
  const [mode, setMode] = useState("Login");
  return (
    <Box>
      <Heading level={3}> Login</Heading>
      <Box gap="small" direction="row" wrap={true}>
        <Button
          href="/signin"
          icon={<Tools />}
          label="Login Using BidBid Account"
          primary
        />
        <br />
        <Button 
          onClick={() => fblogIn(user => assign('/'))}
          icon={<Facebook />} label="Login with Facebook" primary />
      </Box>
      <Heading level={4}>Or Create An Account</Heading>
      Name:
      <TextInput
        value={model["fullname"]}
        onChange={(event) => {
          setModel({
            ...model,
            fullName: event.target.value,
          });
        }}
        placeholder="Name"
      />
      Email:
      <TextInput
        value={model["email"]}
        onChange={(event) => {
          setModel({
            ...model,
            username: event.target.value,
            email: event.target.value,
          });
        }}
        placeholder="email"
      />
      Password:
      <TextInput
        value={model["password"]}
        onChange={(event) => {
          setModel({
            ...model,
            password: event.target.value,
          });
        }}
        type="password"
        placeholder="Password"
      />
      <Box>
        <Anchor href="/reset"> Reset Password </Anchor>
      </Box>
      <Button
        label="Create Account"
        primary
        onClick={() => {
          if (model.fullName && model.email && model.password) {
            Parse.Cloud.run("saveUserProfile", {
              email: model.email,
              fullName: model.fullName,
              password: model.password
            });
          }
        }}
      />
      {message}
    </Box>
  );
};
export default Component;
